import { mapActions, mapGetters, mapState } from "vuex";

export default {
  data() {
    return {};
  },
  methods: {
    ...mapActions(["addProduct", "routerToDetail"]),
  },
  computed: {
    ...mapGetters(["homeProduct",]),
    ...mapState(["allProduct"]),
  },
};
